import * as React from "react";
import Dialog from "@mui/material/Dialog";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Slide from "@mui/material/Slide";
import styled from "styled-components";
import { TiArrowBack } from "react-icons/ti";
import { useDispatch } from "react-redux";
import { reset } from "../../../reduxjs/ViewInfoSlice";
import useAxios from "../../../api/Axios";
import { IoClose } from "react-icons/io5";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function ViewInfo({ classInfo, openBox, id }) {
  const dispatch = useDispatch();
  const axios = useAxios();
  const [backToTable, setBackToTable] = React.useState(false);
  const [viewData, setViewData] = React.useState([]);
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    console.log(id);
    const getData = async () => {
      const url = "/student/single?id=" + id;
      await axios.get(url).then((res) => {
        setViewData(res.data);
        console.log(res.data);
      });
    };
    getData();
    setLoading(false);
  }, [axios, id]);

  const handleClose = () => {
    dispatch(reset());
  };

  return (
    <div>
      <Dialog
        fullScreen
        open={openBox}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <ToolBar
          handleClose={handleClose}
          back={backToTable}
          setBack={setBackToTable}
          classInfo={classInfo}
        />
        <Container>
          <Paper>
            {loading ? "Loading" : <h1>{viewData[0].courseName}</h1>}
          </Paper>
        </Container>
      </Dialog>
    </div>
  );
}

function ToolBar({ handleClose, back, setBack, classInfo }) {
  return (
    <AppBar
      sx={{
        position: "relative",
      }}
    >
      <Toolbar>
        <div style={{ width: "45px" }}>
          <Icon>
            <IconButton
              edge="end"
              color="inherit"
              onClick={handleClose}
              aria-label="close"
            >
              <TiArrowBack />
            </IconButton>
          </Icon>
        </div>

        <Typography
          sx={{
            ml: 2,
            flex: 1,
          }}
          variant="h6"
          component="div"
        >
          View Marks
        </Typography>
        <p
          className="courseTitle"
          style={{
            marginBottom: "0",
            marginRight: "2rem",
          }}
        >
          {classInfo.courseName + " - Term: " + classInfo.term}
        </p>
        <Icon>
          <IconButton color="inherit" onClick={handleClose} aria-label="close">
            <IoClose />
          </IconButton>
        </Icon>
      </Toolbar>
    </AppBar>
  );
}

const Icon = styled.div`
  width: 45px;
  height: 45px;
  border: 2px solid #fff;
  border-radius: 50px;
  display: flex;
  align-items: center;
`;

export const Container = styled.div`
  width: 100%;
  height: 100%;
  background: rgba(100, 100, 100, 1);
  display: flex;
  justify-content: center;
  padding: 10px 0.3rem 2rem;
  overflow-y: scroll;
`;

export const Paper = styled.div`
  width: 800px;
  height: 1100px;
  background: #fff;
  border-radius: 5px;

  @media (max-width: 810px) {
    width: 95%;
  }
`;
